@import '../../../../scss/variable';
@import '../../../../scss/mixins';

.navbar {
  width: 100%;
  // padding: 20px 180px;
  display: flex;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
  z-index: 1000;
  top: 0;
  left: 0;
  right: 0;
  position: fixed;
  justify-content: flex-start;

  // @include breakpoint(tablet) {
  //   padding: 8px 31px;

  //   position: absolute;
  // }
  // @include breakpoint(mobile) {
  //   padding: 8px 10px;
  //   position: absolute;
  // }

  &__quote {
    color: $primary-color;
    font-weight: bold;
    line-height: 19px;
  }
}

.navbar__bar-decoration {
  height: 41px;
  width: 1px;
  background-color: $primary-color;
  margin-inline: 10px;

  @include breakpoint(mobile) {
    height: 30px;
  }
}
