@import '../../../scss/mixins';
@import '../../../scss/variable';

.tabThemeJob {
  position: relative;

  .div-info-themes-item {
    text-align: center;
    margin-top: -55px;
    position: absolute;
    width: 85%;
  }

  .MuiButtonBase-root.MuiButtonBase-root {
    color: white;
    font-weight: bold;
    text-transform: none;
  }
}

.tabThemeJob> {

  :last-child,
  :first-child {
    background-color: #ffffff;
    position: absolute;
    top: 0;
    bottom: 0;
    z-index: 1;

    svg {
      width: 30px;
      height: 30px;
      border-radius: 50%;
      font-size: 50px;
      border: 1px solid #ccc;
      color: #000000;
      background-color: #fff;
    }
  }

  :last-child {
    right: 0px;
    padding-right: 12px;
  }

  :first-child {
    left: 0px;
    padding-left: 12px;
  }
}

.MuiTabs-indicator {
  background-color: $primary-color !important;
}