.post-history {
  margin-top: 70px;
  .post-history_main {
    padding: 50px 180px 0 180px;
    margin: 12px auto;

    .history-post_left {
      flex: 1;
      margin-top: 16px;
    }

    .history-post_right {
      flex: 3;
      margin: 0 12px;
    }
  }
}

.active-child {
  background-color: #0d99ff;
  color: #ffffff;
}

.ant-collapse-item.ant-collapse-item-active.history-left_item {
  border-bottom: 1px solid #aaaaaa;
}

.ant-collapse-header {
  border-bottom: 1px solid #aaaaaa;
}

.history-left_item {
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  .child-item {
    padding: 12px 12px 12px 48px;
    cursor: pointer;
    border-radius: 4px;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    // transition: all 0.1.5s linear;
  }
}

:where(.css-dev-only-do-not-override-yp8pcc).ant-collapse-ghost
  > .ant-collapse-item
  > .ant-collapse-content
  > .ant-collapse-content-box {
  padding-block: 0;
  padding: 0;
}

:where(.css-dev-only-do-not-override-yp8pcc).ant-collapse
  > .ant-collapse-item
  > .ant-collapse-header {
  border-radius: 4px;
  display: flex;
  align-items: center;
  padding: 0 12px;
  .ant-collapse-header-text {
    .panel-title_text {
      padding: 16px 12px;
    }
  }
}

.ant-collapse-item-active .ant-collapse-header {
  background-color: #0d99ff;
  svg {
    color: white;
  }
  .ant-collapse-header-text {
    color: white;
  }
}

.ant-collapse .ant-collapse-item-active .ant-collapse-expand-icon svg,
.ant-collapse .ant-collapse-item-active .activeItem {
  color: #ffffff;
}

.ant-collapse-content {
  margin-left: 12px;
  .ant-collapse-content-box {
    .child-item {
      margin: 4px 0;
    }
    .child-item:hover {
      background-color: #bfcbd4;
      transition: all 0.3s linear;
      color: #000000;
    }
    .active-child.child-item:hover {
      background-color: #1371b5;
      color: #ffffff;
    }
  }
}
